import React, { useState } from 'react';
import { View, Text, StyleSheet, TextInput, Alert, ScrollView, TouchableOpacity } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { useGame } from '../../context/GameContext';
import MinecraftCard from '../../components/MinecraftCard';
import MinecraftButton from '../../components/MinecraftButton';
import { RouletteGame } from '../../utils/games';

export default function RouletteGameScreen() {
  const { balance, playGame } = useGame();
  const [betType, setBetType] = useState(null);
  const [betValue, setBetValue] = useState(null);
  const [betAmount, setBetAmount] = useState('');
  const [isSpinning, setIsSpinning] = useState(false);
  const [lastResult, setLastResult] = useState(null);

  const handleBetType = (type, value = null) => {
    setBetType(type);
    setBetValue(value);
    setBetAmount('');
  };

  const handleNumberBet = (number) => {
    setBetType('number');
    setBetValue(number);
  };

  const handleSpin = async () => {
    if (!betType) {
      Alert.alert('', '  ');
      return;
    }

    if (betType === 'number' && betValue === null) {
      Alert.alert('', ' ');
      return;
    }

    const bet = parseFloat(betAmount);
    
    if (!bet || bet <= 0) {
      Alert.alert('', '   ');
      return;
    }

    if (bet > balance) {
      Alert.alert('', ' ');
      return;
    }

    setIsSpinning(true);
    
    try {
      const result = RouletteGame.spin(betType, bet, betValue);
      await playGame('roulette', bet, result);
      setLastResult(result);
    } catch (error) {
      Alert.alert('', error.message);
    } finally {
      setIsSpinning(false);
    }
  };

  const getColorEmoji = (color) => {
    if (color === 'red') return '';
    if (color === 'black') return '';
    return '';
  };

  return (
    <LinearGradient
      colors={['#7CB342', '#5A8A2E']}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContent}>
        <MinecraftCard title=" ">
          {lastResult && (
            <View style={styles.resultContainer}>
              <Text style={styles.resultText}>
                : {getColorEmoji(lastResult.winningColor)} {lastResult.winningNumber}
              </Text>
              {lastResult.isWin ? (
                <Text style={styles.winText}>
                     {lastResult.winAmount.toFixed(2)} TON!
                </Text>
              ) : (
                <Text style={styles.loseText}>  </Text>
              )}
            </View>
          )}

          <View style={styles.betTypeContainer}>
            <Text style={styles.sectionTitle}> :</Text>
            <View style={styles.betTypeButtons}>
              <TouchableOpacity
                style={[styles.betTypeButton, betType === 'color' && betValue === 'red' && styles.betTypeButtonActive]}
                onPress={() => handleBetType('color', 'red')}
              >
                <Text style={styles.betTypeButtonText}> </Text>
              </TouchableOpacity>
              <TouchableOpacity
                style={[styles.betTypeButton, betType === 'color' && betValue === 'black' && styles.betTypeButtonActive]}
                onPress={() => handleBetType('color', 'black')}
              >
                <Text style={styles.betTypeButtonText}> </Text>
              </TouchableOpacity>
              <TouchableOpacity
                style={[styles.betTypeButton, betType === 'even' && styles.betTypeButtonActive]}
                onPress={() => handleBetType('even')}
              >
                <Text style={styles.betTypeButtonText}> </Text>
              </TouchableOpacity>
              <TouchableOpacity
                style={[styles.betTypeButton, betType === 'odd' && styles.betTypeButtonActive]}
                onPress={() => handleBetType('odd')}
              >
                <Text style={styles.betTypeButtonText}> </Text>
              </TouchableOpacity>
            </View>
          </View>

          {betType === 'number' && (
            <View style={styles.numberContainer}>
              <Text style={styles.sectionTitle}>  (0-36):</Text>
              <View style={styles.numberGrid}>
                {Array.from({ length: 37 }, (_, i) => (
                  <TouchableOpacity
                    key={i}
                    style={[
                      styles.numberButton,
                      betValue === i && styles.numberButtonActive,
                      i === 0 && styles.greenNumber,
                      i > 0 && RouletteGame.COLORS.red.includes(i) && styles.redNumber,
                      i > 0 && RouletteGame.COLORS.black.includes(i) && styles.blackNumber,
                    ]}
                    onPress={() => handleNumberBet(i)}
                  >
                    <Text style={styles.numberButtonText}>{i}</Text>
                  </TouchableOpacity>
                ))}
              </View>
            </View>
          )}

          {betType && betType !== 'number' && (
            <View style={styles.betContainer}>
              <Text style={styles.label}>  (TON):</Text>
              <TextInput
                style={styles.input}
                value={betAmount}
                onChangeText={setBetAmount}
                keyboardType="decimal-pad"
                placeholder="0.1"
                placeholderTextColor="#9E9E9E"
              />
            </View>
          )}

          {betType === 'number' && betValue !== null && (
            <View style={styles.betContainer}>
              <Text style={styles.label}>  (TON):</Text>
              <TextInput
                style={styles.input}
                value={betAmount}
                onChangeText={setBetAmount}
                keyboardType="decimal-pad"
                placeholder="0.1"
                placeholderTextColor="#9E9E9E"
              />
            </View>
          )}

          {betType && (
            <>
              <Text style={styles.balanceText}>
                : {balance.toFixed(2)} TON
              </Text>
              <MinecraftButton
                title={isSpinning ? "..." : " "}
                onPress={handleSpin}
                disabled={isSpinning || !betAmount || (betType === 'number' && betValue === null)}
                style={styles.spinButton}
              />
            </>
          )}
        </MinecraftCard>
      </ScrollView>
    </LinearGradient>
  );
}

