import React, { useState } from 'react';
import { View, Text, StyleSheet, TextInput, Alert, ScrollView } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { useGame } from '../../context/GameContext';
import MinecraftCard from '../../components/MinecraftCard';
import MinecraftButton from '../../components/MinecraftButton';
import { SlotsGame } from '../../utils/games';

export default function SlotsGameScreen() {
  const { balance, playGame } = useGame();
  const [betAmount, setBetAmount] = useState('');
  const [isSpinning, setIsSpinning] = useState(false);
  const [lastResult, setLastResult] = useState(null);

  const handleSpin = async () => {
    const bet = parseFloat(betAmount);
    
    if (!bet || bet <= 0) {
      Alert.alert('', '   ');
      return;
    }

    if (bet > balance) {
      Alert.alert('', ' ');
      return;
    }

    setIsSpinning(true);
    
    try {
      const result = SlotsGame.spin(bet);
      await playGame('slots', bet, result);
      setLastResult(result);
    } catch (error) {
      Alert.alert('', error.message);
    } finally {
      setIsSpinning(false);
    }
  };

  return (
    <LinearGradient
      colors={['#7CB342', '#5A8A2E']}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContent}>
        <MinecraftCard title=" ">
          {lastResult && (
            <View style={styles.resultContainer}>
              <View style={styles.reelsContainer}>
                <Text style={styles.reel}>{lastResult.reels[0]}</Text>
                <Text style={styles.reel}>{lastResult.reels[1]}</Text>
                <Text style={styles.reel}>{lastResult.reels[2]}</Text>
              </View>
              {lastResult.isWin ? (
                <Text style={styles.winText}>
                     {lastResult.winAmount.toFixed(2)} TON!
                </Text>
              ) : (
                <Text style={styles.loseText}>  </Text>
              )}
            </View>
          )}

          <View style={styles.betContainer}>
            <Text style={styles.label}>  (TON):</Text>
            <TextInput
              style={styles.input}
              value={betAmount}
              onChangeText={setBetAmount}
              keyboardType="decimal-pad"
              placeholder="0.1"
              placeholderTextColor="#9E9E9E"
            />
            <Text style={styles.balanceText}>
              : {balance.toFixed(2)} TON
            </Text>
          </View>

          <MinecraftButton
            title={isSpinning ? "..." : " "}
            onPress={handleSpin}
            disabled={isSpinning || !betAmount}
            style={styles.spinButton}
          />

          <View style={styles.payoutsContainer}>
            <Text style={styles.payoutsTitle}> :</Text>
            <Text style={styles.payoutItem}> - x50</Text>
            <Text style={styles.payoutItem}> - x30</Text>
            <Text style={styles.payoutItem}> - x25</Text>
            <Text style={styles.payoutItem}> - x10</Text>
            <Text style={styles.payoutItem}> - x5</Text>
            <Text style={styles.payoutItem}> - x3</Text>
            <Text style={styles.payoutItem}>  - x2</Text>
          </View>
        </MinecraftCard>
      </ScrollView>
    </LinearGradient>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollContent: {
    padding: 16,
  },
  resultContainer: {
    alignItems: 'center',
    marginBottom: 20,
    padding: 16,
    backgroundColor: 'rgba(0, 0, 0, 0.2)',
    borderRadius: 8,
  },
  reelsContainer: {
    flexDirection: 'row',
    justifyContent: 'center',
    marginBottom: 16,
  },
  reel: {
    fontSize: 64,
    marginHorizontal: 8,
    padding: 16,
    backgroundColor: 'rgba(0, 0, 0, 0.3)',
    borderRadius: 8,
  },
  winText: {
    fontSize: 18,
    color: '#FFD700',
    fontWeight: 'bold',
  },
  loseText: {
    fontSize: 18,
    color: '#F44336',
  },
  betContainer: {
    marginBottom: 16,
  },
  label: {
    fontSize: 16,
    color: '#FFFFFF',
    marginBottom: 8,
  },
  input: {
    backgroundColor: 'rgba(0, 0, 0, 0.3)',
    borderWidth: 2,
    borderColor: '#5A8A2E',
    borderRadius: 8,
    padding: 12,
    color: '#FFFFFF',
    fontSize: 18,
    marginBottom: 8,
  },
  balanceText: {
    fontSize: 14,
    color: '#FFD700',
  },
  spinButton: {
    marginBottom: 16,
  },
  payoutsContainer: {
    marginTop: 16,
    padding: 12,
    backgroundColor: 'rgba(0, 0, 0, 0.2)',
    borderRadius: 8,
  },
  payoutsTitle: {
    fontSize: 16,
    fontWeight: 'bold',
    color: '#FFD700',
    marginBottom: 8,
  },
  payoutItem: {
    fontSize: 14,
    color: '#FFFFFF',
    marginBottom: 4,
  },
});
