import React from 'react';
import { View, Text, StyleSheet, ScrollView } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { useGame } from '../context/GameContext';
import { useWallet } from '../context/WalletContext';
import MinecraftCard from '../components/MinecraftCard';
import MinecraftButton from '../components/MinecraftButton';

export default function ProfileScreen() {
  const { balance, gameHistory } = useGame();
  const { isConnected, walletAddress, disconnectWallet } = useWallet();

  const totalGames = gameHistory.length;
  const totalWins = gameHistory.filter(g => g.isWin).length;
  const totalWinnings = gameHistory
    .filter(g => g.isWin)
    .reduce((sum, g) => sum + (g.winAmount || 0), 0);

  return (
    <LinearGradient
      colors={['#7CB342', '#5A8A2E']}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContent}>
        <MinecraftCard title=" ">
          <View style={styles.profileInfo}>
            <Text style={styles.profileLabel}>:</Text>
            <Text style={styles.profileValue}>
               {balance.toFixed(2)} TON
            </Text>
          </View>
          <View style={styles.profileInfo}>
            <Text style={styles.profileLabel}>:</Text>
            <Text style={styles.profileValue}>
              {isConnected ? ' ' : '  '}
            </Text>
          </View>
          {isConnected && walletAddress && (
            <View style={styles.profileInfo}>
              <Text style={styles.profileLabel}>:</Text>
              <Text style={styles.profileAddress} numberOfLines={1}>
                {walletAddress.substring(0, 20)}...
              </Text>
            </View>
          )}
        </MinecraftCard>

        <MinecraftCard title=" ">
          <View style={styles.statsRow}>
            <View style={styles.statItem}>
              <Text style={styles.statValue}>{totalGames}</Text>
              <Text style={styles.statLabel}> </Text>
            </View>
            <View style={styles.statItem}>
              <Text style={styles.statValue}>{totalWins}</Text>
              <Text style={styles.statLabel}></Text>
            </View>
            <View style={styles.statItem}>
              <Text style={styles.statValue}>
                {totalWinnings.toFixed(2)}
              </Text>
              <Text style={styles.statLabel}>TON </Text>
            </View>
          </View>
        </MinecraftCard>

        {isConnected && (
          <MinecraftButton
            title=" "
            onPress={disconnectWallet}
            style={styles.disconnectButton}
          />
        )}
      </ScrollView>
    </LinearGradient>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollContent: {
    padding: 16,
  },
  profileInfo: {
    marginBottom: 16,
  },
  profileLabel: {
    fontSize: 14,
    color: '#FFFFFF',
    opacity: 0.8,
    marginBottom: 4,
  },
  profileValue: {
    fontSize: 18,
    color: '#FFD700',
    fontWeight: 'bold',
  },
  profileAddress: {
    fontSize: 12,
    color: '#FFD700',
    fontFamily: 'monospace',
  },
  statsRow: {
    flexDirection: 'row',
    justifyContent: 'space-around',
  },
  statItem: {
    alignItems: 'center',
  },
  statValue: {
    fontSize: 24,
    fontWeight: 'bold',
    color: '#FFD700',
    marginBottom: 4,
  },
  statLabel: {
    fontSize: 12,
    color: '#FFFFFF',
    opacity: 0.8,
  },
  disconnectButton: {
    marginTop: 16,
  },
});
