import React, { useState, useEffect } from 'react';
import { NavigationContainer } from '@react-navigation/native';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { createStackNavigator } from '@react-navigation/stack';
import { StatusBar } from 'expo-status-bar';
import { StyleSheet, Text, View, ActivityIndicator, Platform } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { isTelegramWeb, getTelegramUser, expandTelegramWebApp } from './src/utils/telegram';
import { apiService } from './src/services/api';

// Screens
import AuthScreen from './src/screens/AuthScreen';
import HomeScreen from './src/screens/HomeScreen';
import GamesScreen from './src/screens/GamesScreen';
import WalletScreen from './src/screens/WalletScreen';
import HistoryScreen from './src/screens/HistoryScreen';
import ProfileScreen from './src/screens/ProfileScreen';

// Game Screens
import SlotsGameScreen from './src/screens/games/SlotsGameScreen';
import RouletteGameScreen from './src/screens/games/RouletteGameScreen';
import CoinFlipGameScreen from './src/screens/games/CoinFlipGameScreen';
import DiceGameScreen from './src/screens/games/DiceGameScreen';

// Wallet Screens
import DepositScreen from './src/screens/wallet/DepositScreen';
import WithdrawScreen from './src/screens/wallet/WithdrawScreen';
import ConnectWalletScreen from './src/screens/wallet/ConnectWalletScreen';

import { GameProvider } from './src/context/GameContext';
import { WalletProvider } from './src/context/WalletContext';

const Tab = createBottomTabNavigator();
const Stack = createStackNavigator();

function MainTabs() {
  return (
    <Tab.Navigator
      screenOptions={{
        headerStyle: {
          backgroundColor: '#7CB342',
        },
        headerTintColor: '#fff',
        headerTitleStyle: {
          fontFamily: 'Minecraft',
          fontSize: 20,
        },
        tabBarStyle: {
          backgroundColor: '#8D6E63',
          borderTopColor: '#5A8A2E',
          borderTopWidth: 2,
        },
        tabBarActiveTintColor: '#FFD700',
        tabBarInactiveTintColor: '#9E9E9E',
        tabBarLabelStyle: {
          fontFamily: 'Minecraft',
          fontSize: 12,
        },
      }}
    >
      <Tab.Screen 
        name="Home" 
        component={HomeScreen}
        options={{
          tabBarLabel: ' ',
          headerTitle: ' Minecraft Casino',
        }}
      />
      <Tab.Screen 
        name="Games" 
        component={GamesScreen}
        options={{
          tabBarLabel: ' ',
          headerTitle: ' ',
        }}
      />
      <Tab.Screen 
        name="Wallet" 
        component={WalletScreen}
        options={{
          tabBarLabel: ' ',
          headerTitle: ' ',
        }}
      />
      <Tab.Screen 
        name="History" 
        component={HistoryScreen}
        options={{
          tabBarLabel: ' ',
          headerTitle: ' ',
        }}
      />
      <Tab.Screen 
        name="Profile" 
        component={ProfileScreen}
        options={{
          tabBarLabel: ' ',
          headerTitle: ' ',
        }}
      />
    </Tab.Navigator>
  );
}

export default function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    initializeApp();
  }, []);

  const initializeApp = async () => {
    //    (5 )
    const timeoutId = setTimeout(() => {
      console.warn('Initialization timeout, showing auth screen');
      setIsLoading(false);
    }, 5000);

    try {
      //  Telegram Web App
      if (isTelegramWeb()) {
        try {
          expandTelegramWebApp();
        } catch (error) {
          console.warn('Error expanding Telegram Web App:', error);
        }
        
        // setHeaderColor  setBackgroundColor     6.0+
        // setTelegramHeaderColor('#7CB342');
        // setTelegramBackgroundColor('#7CB342');
        
        //    Telegram
        const telegramUser = getTelegramUser();
        if (telegramUser && telegramUser.id) {
          try {
            //   API  (3 )
            const authPromise = apiService.authTelegram(telegramUser.id);
            const timeoutPromise = new Promise((_, reject) => 
              setTimeout(() => reject(new Error('Auth timeout')), 3000)
            );
            
            await Promise.race([authPromise, timeoutPromise]);
            clearTimeout(timeoutId);
            setIsAuthenticated(true);
            setIsLoading(false);
            return;
          } catch (error) {
            console.warn('Error auto-auth from Telegram (will show auth screen):', error.message);
            //     
          }
        }
      }
      
      //   
      await checkAuth();
      clearTimeout(timeoutId);
    } catch (error) {
      console.error('Error initializing app:', error);
      clearTimeout(timeoutId);
      setIsLoading(false);
    }
  };

  const checkAuth = async () => {
    try {
      //   AsyncStorage (1 )
      const storagePromise = AsyncStorage.getItem('api_key');
      const timeoutPromise = new Promise((resolve) => 
        setTimeout(() => resolve(null), 1000)
      );
      
      const apiKey = await Promise.race([storagePromise, timeoutPromise]);
      setIsAuthenticated(!!apiKey);
    } catch (error) {
      console.error('Error checking auth:', error);
      //      
      setIsAuthenticated(false);
    } finally {
      setIsLoading(false);
    }
  };

  if (isLoading) {
    return (
      <View style={styles.loadingContainer}>
        <ActivityIndicator size="large" color="#7CB342" />
        <Text style={styles.loadingText}>...</Text>
      </View>
    );
  }

  return (
    <WalletProvider>
      <GameProvider>
        <NavigationContainer>
          <StatusBar style="light" />
          <Stack.Navigator
            screenOptions={{
              headerStyle: {
                backgroundColor: '#7CB342',
              },
              headerTintColor: '#fff',
              headerTitleStyle: {
                fontFamily: 'Minecraft',
                fontSize: 20,
              },
            }}
          >
            {!isAuthenticated ? (
              <Stack.Screen 
                name="Auth" 
                component={AuthScreen}
                options={{ headerShown: false }}
                initialParams={{ onAuthSuccess: () => setIsAuthenticated(true) }}
              />
            ) : (
              <>
                <Stack.Screen 
                  name="Main" 
                  component={MainTabs}
                  options={{ headerShown: false }}
                />
            
                {/* Game Screens */}
                <Stack.Screen 
                  name="SlotsGame" 
                  component={SlotsGameScreen}
                  options={{ title: ' ' }}
                />
                <Stack.Screen 
                  name="RouletteGame" 
                  component={RouletteGameScreen}
                  options={{ title: ' ' }}
                />
                <Stack.Screen 
                  name="CoinFlipGame" 
                  component={CoinFlipGameScreen}
                  options={{ title: ' /' }}
                />
                <Stack.Screen 
                  name="DiceGame" 
                  component={DiceGameScreen}
                  options={{ title: ' ' }}
                />
                
                {/* Wallet Screens */}
                <Stack.Screen 
                  name="ConnectWallet" 
                  component={ConnectWalletScreen}
                  options={{ title: '  ' }}
                />
                <Stack.Screen 
                  name="Deposit" 
                  component={DepositScreen}
                  options={{ title: ' ' }}
                />
                <Stack.Screen 
                  name="Withdraw" 
                  component={WithdrawScreen}
                  options={{ title: ' ' }}
                />
              </>
            )}
          </Stack.Navigator>
        </NavigationContainer>
      </GameProvider>
    </WalletProvider>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#7CB342',
  },
  loadingContainer: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#7CB342',
  },
  loadingText: {
    marginTop: 16,
    fontSize: 16,
    color: '#FFFFFF',
  },
});
