// API     
import axios from 'axios';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { Platform } from 'react-native';

// URL API  (   )
const getApiUrl = () => {
  if (Platform.OS === 'web') {
    //  - (Mini App)  localhost  
    //      URL  API 
    if (window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1') {
      return 'http://localhost:8000';
    }
    //    URL  API 
    // : 'https://your-api-domain.com'  'https://api.yourdomain.com'
    return 'http://localhost:8000'; // :    URL API
  }
  
  if (__DEV__) {
    return 'http://localhost:8000';
  }
  
  return process.env.EXPO_PUBLIC_API_URL || 'https://your-api-domain.com';
};

const API_URL = getApiUrl();

const api = axios.create({
  baseURL: API_URL,
  timeout: 5000, //    5 
  headers: {
    'Content-Type': 'application/json',
  },
});

//    API 
api.interceptors.request.use(
  async (config) => {
    try {
      const apiKey = await AsyncStorage.getItem('api_key');
      if (apiKey) {
        config.headers['X-API-Key'] = apiKey;
      }
    } catch (error) {
      console.warn('Error getting API key from storage:', error);
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

//    
api.interceptors.response.use(
  (response) => response,
  (error) => {
    //      API
    if (error.code === 'ECONNABORTED' || error.message.includes('timeout')) {
      console.warn('API timeout - server may be unavailable');
    } else if (error.request && !error.response) {
      console.warn('API server unavailable - check API_URL:', API_URL);
    } else if (error.response) {
      console.error('API Error:', error.response.status, error.response.data);
    } else {
      console.error('API Error:', error.message);
    }
    return Promise.reject(error);
  }
);

// API 
export const apiService = {
  // 
  async authTelegram(telegramId) {
    const response = await api.get(`/api/auth/telegram?telegram_id=${telegramId}`);
    await AsyncStorage.setItem('api_key', response.data.api_key);
    await AsyncStorage.setItem('user_id', response.data.user_id.toString());
    return response.data;
  },

  // 
  async getCurrentUser() {
    const response = await api.get('/api/user/me');
    return response.data;
  },

  async getBalance() {
    const response = await api.get('/api/user/balance');
    return response.data;
  },

  // 
  async getWallet() {
    const response = await api.get('/api/wallet');
    return response.data;
  },

  async connectWallet(walletAddress) {
    const response = await api.post('/api/wallet/connect', {
      wallet_address: walletAddress,
    });
    return response.data;
  },

  // 
  async getDepositAddress() {
    const response = await api.get('/api/deposit/address');
    return response.data;
  },

  async verifyDeposit(txHash) {
    const response = await api.post('/api/deposit/verify', {
      tx_hash: txHash,
    });
    return response.data;
  },

  // 
  async withdraw(address, amount) {
    const response = await api.post('/api/withdraw', {
      address,
      amount,
    });
    return response.data;
  },

  // 
  async playGame(gameType, betAmount, betValue = null) {
    const response = await api.post('/api/games/play', {
      game_type: gameType,
      bet_amount: betAmount,
      bet_value: betValue,
    });
    return response.data;
  },

  // 
  async getTransactions(limit = 10) {
    const response = await api.get(`/api/transactions?limit=${limit}`);
    return response.data;
  },

  async getGameHistory(limit = 10) {
    const response = await api.get(`/api/games/history?limit=${limit}`);
    return response.data;
  },

  // 
  async logout() {
    await AsyncStorage.removeItem('api_key');
    await AsyncStorage.removeItem('user_id');
  },
};

export default apiService;
