import React from 'react';
import { View, Text, StyleSheet, ScrollView } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { useGame } from '../context/GameContext';
import { useWallet } from '../context/WalletContext';
import MinecraftCard from '../components/MinecraftCard';
import MinecraftButton from '../components/MinecraftButton';
import { useNavigation } from '@react-navigation/native';

export default function HomeScreen() {
  const { balance } = useGame();
  const { isConnected, walletAddress } = useWallet();
  const navigation = useNavigation();

  const formatBalance = (amount) => {
    if (amount >= 1) {
      return `${amount.toFixed(2)} TON`;
    }
    return `${(amount * 1000).toFixed(0)} TON`;
  };

  return (
    <LinearGradient
      colors={['#7CB342', '#5A8A2E']}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContent}>
        <View style={styles.header}>
          <Text style={styles.title}> MINECRAFT CASINO </Text>
        </View>

        <MinecraftCard title="  ">
          <View style={styles.balanceContainer}>
            <Text style={styles.balanceAmount}> {formatBalance(balance)}</Text>
            <Text style={styles.balanceLabel}>  </Text>
          </View>
        </MinecraftCard>

        <MinecraftCard title=" ">
          <View style={styles.walletInfo}>
            {isConnected ? (
              <>
                <Text style={styles.walletStatus}> </Text>
                <Text style={styles.walletAddress} numberOfLines={1}>
                  {walletAddress?.substring(0, 20)}...
                </Text>
              </>
            ) : (
              <>
                <Text style={styles.walletStatus}>  </Text>
                <Text style={styles.walletHint}>
                   TON-  
                </Text>
              </>
            )}
          </View>
        </MinecraftCard>

        <View style={styles.quickActions}>
          <MinecraftButton
            title=" "
            onPress={() => navigation.navigate('Games')}
            style={styles.actionButton}
          />
          <MinecraftButton
            title=" "
            onPress={() => navigation.navigate('Wallet')}
            style={styles.actionButton}
          />
        </View>

        <MinecraftCard title=" ">
          <View style={styles.statsContainer}>
            <View style={styles.statItem}>
              <Text style={styles.statValue}>0</Text>
              <Text style={styles.statLabel}> </Text>
            </View>
            <View style={styles.statItem}>
              <Text style={styles.statValue}>0</Text>
              <Text style={styles.statLabel}></Text>
            </View>
            <View style={styles.statItem}>
              <Text style={styles.statValue}>0 TON</Text>
              <Text style={styles.statLabel}></Text>
            </View>
          </View>
        </MinecraftCard>
      </ScrollView>
    </LinearGradient>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollContent: {
    padding: 16,
  },
  header: {
    alignItems: 'center',
    marginVertical: 20,
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    color: '#FFD700',
    textShadowColor: '#000',
    textShadowOffset: { width: 2, height: 2 },
    textShadowRadius: 4,
  },
  balanceContainer: {
    alignItems: 'center',
    paddingVertical: 20,
  },
  balanceAmount: {
    fontSize: 32,
    fontWeight: 'bold',
    color: '#FFD700',
    marginBottom: 8,
    textShadowColor: '#000',
    textShadowOffset: { width: 1, height: 1 },
    textShadowRadius: 2,
  },
  balanceLabel: {
    fontSize: 14,
    color: '#FFFFFF',
    opacity: 0.8,
  },
  walletInfo: {
    alignItems: 'center',
  },
  walletStatus: {
    fontSize: 16,
    color: '#FFFFFF',
    marginBottom: 8,
  },
  walletAddress: {
    fontSize: 12,
    color: '#FFD700',
    fontFamily: 'monospace',
  },
  walletHint: {
    fontSize: 12,
    color: '#FFFFFF',
    opacity: 0.7,
    textAlign: 'center',
  },
  quickActions: {
    flexDirection: 'row',
    justifyContent: 'space-around',
    marginVertical: 16,
  },
  actionButton: {
    flex: 1,
    marginHorizontal: 8,
  },
  statsContainer: {
    flexDirection: 'row',
    justifyContent: 'space-around',
  },
  statItem: {
    alignItems: 'center',
  },
  statValue: {
    fontSize: 20,
    fontWeight: 'bold',
    color: '#FFD700',
    marginBottom: 4,
  },
  statLabel: {
    fontSize: 12,
    color: '#FFFFFF',
    opacity: 0.8,
  },
});
