import React, { createContext, useState, useContext, useEffect } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { apiService } from '../services/api';

const WalletContext = createContext();

export const useWallet = () => {
  const context = useContext(WalletContext);
  if (!context) {
    throw new Error('useWallet must be used within WalletProvider');
  }
  return context;
};

export const WalletProvider = ({ children }) => {
  const [walletAddress, setWalletAddress] = useState(null);
  const [isConnected, setIsConnected] = useState(false);
  const [transactions, setTransactions] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    loadWallet();
    loadTransactions();
    //   10 
    const interval = setInterval(() => {
      loadWallet();
      loadTransactions();
    }, 10000);
    return () => clearInterval(interval);
  }, []);

  const loadWallet = async () => {
    try {
      const apiKey = await AsyncStorage.getItem('api_key');
      if (!apiKey) {
        setIsLoading(false);
        return;
      }

      const wallet = await apiService.getWallet();
      if (wallet) {
        setWalletAddress(wallet.wallet_address);
        setIsConnected(true);
      } else {
        setWalletAddress(null);
        setIsConnected(false);
      }
    } catch (error) {
      console.error('Error loading wallet:', error);
      // Fallback   
      const address = await AsyncStorage.getItem('walletAddress');
      if (address) {
        setWalletAddress(address);
        setIsConnected(true);
      }
    } finally {
      setIsLoading(false);
    }
  };

  const loadTransactions = async () => {
    try {
      const apiKey = await AsyncStorage.getItem('api_key');
      if (!apiKey) return;

      const txData = await apiService.getTransactions(20);
      setTransactions(txData.map(tx => ({
        id: tx.id,
        type: tx.transaction_type,
        amount: tx.amount / 1_000_000_000,
        status: tx.status,
        timestamp: tx.created_at,
      })));
    } catch (error) {
      console.error('Error loading transactions:', error);
    }
  };

  const connectWallet = async (address) => {
    try {
      if (!validateTONAddress(address)) {
        throw new Error('   TON-');
      }

      const wallet = await apiService.connectWallet(address);
      setWalletAddress(wallet.wallet_address);
      setIsConnected(true);
      //    
      await AsyncStorage.setItem('walletAddress', address);
      return wallet;
    } catch (error) {
      console.error('Error connecting wallet:', error);
      throw error;
    }
  };

  const disconnectWallet = async () => {
    try {
      setWalletAddress(null);
      setIsConnected(false);
      await AsyncStorage.removeItem('walletAddress');
    } catch (error) {
      console.error('Error disconnecting wallet:', error);
    }
  };

  const addTransaction = async (transaction) => {
    //   
    await loadTransactions();
  };

  const validateTONAddress = (address) => {
    if (!address || address.length < 48) {
      return false;
    }
    return address.startsWith('EQ') || 
           address.startsWith('UQ') || 
           address.startsWith('0:') || 
           address.startsWith('1:');
  };

  const formatBalance = (nano) => {
    const ton = nano / 1_000_000_000;
    if (ton >= 1) {
      return `${ton.toFixed(2)} TON`;
    }
    return `${nano} TON`;
  };

  const refreshData = async () => {
    await loadWallet();
    await loadTransactions();
  };

  const value = {
    walletAddress,
    isConnected,
    transactions,
    isLoading,
    connectWallet,
    disconnectWallet,
    addTransaction,
    formatBalance,
    refreshData,
  };

  return <WalletContext.Provider value={value}>{children}</WalletContext.Provider>;
};
