export const SlotsGame = {
  SYMBOLS: ["", "", "", "", "", ""],
  SYMBOL_NAMES: {
    "": "",
    "": "",
    "": "",
    "": "",
    "": "",
    "": ""
  },
  PAYOUTS: {
    "": 50,
    "": 30,
    "": 25,
    "": 10,
    "": 5,
    "": 3,
  },
  
  spin(bet) {
    const reels = [
      this.SYMBOLS[Math.floor(Math.random() * this.SYMBOLS.length)],
      this.SYMBOLS[Math.floor(Math.random() * this.SYMBOLS.length)],
      this.SYMBOLS[Math.floor(Math.random() * this.SYMBOLS.length)],
    ];
    
    let winAmount = 0;
    const reelString = reels.join('');
    
    //  
    if (this.PAYOUTS[reelString]) {
      winAmount = bet * this.PAYOUTS[reelString];
    }
    
    //   
    if (reels[0] === reels[1] || reels[1] === reels[2] || reels[0] === reels[2]) {
      winAmount += bet * 2;
    }
    
    return {
      reels,
      winAmount,
      isWin: winAmount > 0,
      symbolsText: reels.map(s => this.SYMBOL_NAMES[s] || s).join(' | '),
    };
  }
};

export const RouletteGame = {
  COLORS: {
    red: [1, 3, 5, 7, 9, 12, 14, 16, 18, 19, 21, 23, 25, 27, 30, 32, 34, 36],
    black: [2, 4, 6, 8, 10, 11, 13, 15, 17, 20, 22, 24, 26, 28, 29, 31, 33, 35],
    green: [0]
  },
  
  spin(betType, betAmount, betValue) {
    const winningNumber = Math.floor(Math.random() * 37);
    let winAmount = 0;
    
    let winningColor = null;
    for (const [color, numbers] of Object.entries(this.COLORS)) {
      if (numbers.includes(winningNumber)) {
        winningColor = color;
        break;
      }
    }
    
    if (betType === 'color') {
      if (betValue === winningColor) {
        winAmount = betAmount * 2;
      }
    } else if (betType === 'number') {
      if (betValue === winningNumber) {
        winAmount = betAmount * 36;
      }
    } else if (betType === 'even') {
      if (winningNumber > 0 && winningNumber % 2 === 0) {
        winAmount = betAmount * 2;
      }
    } else if (betType === 'odd') {
      if (winningNumber > 0 && winningNumber % 2 === 1) {
        winAmount = betAmount * 2;
      }
    }
    
    return {
      winningNumber,
      winningColor,
      winAmount,
      isWin: winAmount > 0,
      betType,
      betValue,
    };
  }
};

export const Minigame = {
  coinFlip(bet, choice) {
    const result = Math.random() < 0.5 ? 'heads' : 'tails';
    const winAmount = result === choice ? bet * 2 : 0;
    
    return {
      result,
      winAmount,
      isWin: winAmount > 0,
      choice,
    };
  },
  
  diceRoll(bet, prediction) {
    const result = Math.floor(Math.random() * 6) + 1;
    const winAmount = result === prediction ? bet * 6 : 0;
    
    return {
      result,
      winAmount,
      isWin: winAmount > 0,
      prediction,
    };
  }
};
