import React, { useState, useEffect } from 'react';
import { View, Text, StyleSheet, TextInput, Alert, ScrollView, Platform } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { useNavigation } from '@react-navigation/native';
import MinecraftCard from '../components/MinecraftCard';
import MinecraftButton from '../components/MinecraftButton';
import { apiService } from '../services/api';
import { isTelegramWeb, getTelegramUser } from '../utils/telegram';

export default function AuthScreen() {
  const navigation = useNavigation();
  const [telegramId, setTelegramId] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    //   Telegram ID    Telegram Web App
    if (isTelegramWeb()) {
      const telegramUser = getTelegramUser();
      if (telegramUser && telegramUser.id) {
        setTelegramId(telegramUser.id.toString());
      }
    }
  }, []);

  const handleAuth = async () => {
    if (!telegramId.trim()) {
      Alert.alert('', ' Telegram ID');
      return;
    }

    const id = parseInt(telegramId);
    if (isNaN(id)) {
      Alert.alert('', 'Telegram ID   ');
      return;
    }

    setIsLoading(true);
    try {
      await apiService.authTelegram(id);
      Alert.alert('', '  !', [
        { 
          text: 'OK', 
          onPress: () => {
            navigation.reset({
              index: 0,
              routes: [{ name: 'Main' }],
            });
          }
        }
      ]);
    } catch (error) {
      console.error('Auth error:', error);
      Alert.alert(
        '', 
        error.response?.data?.detail || error.message || '  . ,  API  .'
      );
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <LinearGradient
      colors={['#7CB342', '#5A8A2E']}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContent}>
        <View style={styles.header}>
          <Text style={styles.title}> MINECRAFT CASINO </Text>
          <Text style={styles.subtitle}>  </Text>
        </View>

        <MinecraftCard title=" ">
          <Text style={styles.description}>
                Telegram ID.
                  @userinfobot  Telegram.
          </Text>

          <View style={styles.inputContainer}>
            <Text style={styles.label}>Telegram ID:</Text>
            <TextInput
              style={styles.input}
              value={telegramId}
              onChangeText={setTelegramId}
              keyboardType="number-pad"
              placeholder="123456789"
              placeholderTextColor="#9E9E9E"
            />
            <Text style={styles.hint}>
               ID: @userinfobot  Telegram
            </Text>
          </View>

          <MinecraftButton
            title={isLoading ? "..." : ""}
            onPress={handleAuth}
            disabled={isLoading || !telegramId.trim()}
            style={styles.authButton}
          />
        </MinecraftCard>

        <MinecraftCard title=" ">
          <Text style={styles.infoText}>
                 Telegram {'\n'}
                  {'\n'}
                
          </Text>
        </MinecraftCard>
      </ScrollView>
    </LinearGradient>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollContent: {
    padding: 16,
    justifyContent: 'center',
    minHeight: '100%',
  },
  header: {
    alignItems: 'center',
    marginBottom: 30,
  },
  title: {
    fontSize: 28,
    fontWeight: 'bold',
    color: '#FFD700',
    textShadowColor: '#000',
    textShadowOffset: { width: 2, height: 2 },
    textShadowRadius: 4,
    marginBottom: 8,
  },
  subtitle: {
    fontSize: 18,
    color: '#FFFFFF',
    opacity: 0.8,
  },
  description: {
    fontSize: 14,
    color: '#FFFFFF',
    opacity: 0.8,
    marginBottom: 20,
    textAlign: 'center',
  },
  inputContainer: {
    marginBottom: 20,
  },
  label: {
    fontSize: 16,
    color: '#FFFFFF',
    marginBottom: 8,
  },
  input: {
    backgroundColor: 'rgba(0, 0, 0, 0.3)',
    borderWidth: 2,
    borderColor: '#5A8A2E',
    borderRadius: 8,
    padding: 12,
    color: '#FFFFFF',
    fontSize: 18,
    marginBottom: 8,
  },
  hint: {
    fontSize: 12,
    color: '#FFD700',
    opacity: 0.8,
  },
  authButton: {
    marginTop: 8,
  },
  infoText: {
    fontSize: 14,
    color: '#FFFFFF',
    opacity: 0.8,
    lineHeight: 20,
  },
});
