import React, { createContext, useState, useContext, useEffect } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { apiService } from '../services/api';

const GameContext = createContext();

export const useGame = () => {
  const context = useContext(GameContext);
  if (!context) {
    throw new Error('useGame must be used within GameProvider');
  }
  return context;
};

export const GameProvider = ({ children }) => {
  const [balance, setBalance] = useState(0);
  const [gameHistory, setGameHistory] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  //    API
  useEffect(() => {
    loadData();
    //    5 
    const interval = setInterval(loadData, 5000);
    return () => clearInterval(interval);
  }, []);

  const loadData = async () => {
    try {
      const apiKey = await AsyncStorage.getItem('api_key');
      if (!apiKey) {
        setIsLoading(false);
        return;
      }

      //  
      const balanceData = await apiService.getBalance();
      setBalance(balanceData.balance_ton || 0);

      //   
      const history = await apiService.getGameHistory(20);
      setGameHistory(history.map(item => ({
        id: item.id,
        gameType: item.game_type,
        betAmount: item.bet_amount / 1_000_000_000,
        winAmount: item.win_amount / 1_000_000_000,
        isWin: item.win_amount > 0,
        timestamp: item.created_at,
      })));
    } catch (error) {
      console.error('Error loading data:', error);
      // Fallback      API
      const savedBalance = await AsyncStorage.getItem('balance');
      if (savedBalance !== null) {
        setBalance(parseFloat(savedBalance));
      }
    } finally {
      setIsLoading(false);
    }
  };

  const updateBalance = async (amount) => {
    const newBalance = balance + amount;
    setBalance(newBalance);
    //    
    try {
      await AsyncStorage.setItem('balance', newBalance.toString());
    } catch (error) {
      console.error('Error saving balance:', error);
    }
  };

  const playGame = async (gameType, betAmount, betValue = null) => {
    try {
      //   API
      const result = await apiService.playGame(gameType, betAmount, betValue);
      
      //  
      setBalance(result.balance / 1_000_000_000);
      
      //  
      await loadData();
      
      return result.result;
    } catch (error) {
      console.error('Error playing game:', error);
      throw error;
    }
  };

  const refreshData = async () => {
    await loadData();
  };

  const value = {
    balance,
    gameHistory,
    isLoading,
    updateBalance,
    playGame,
    refreshData,
  };

  return <GameContext.Provider value={value}>{children}</GameContext.Provider>;
};
