import React from 'react';
import { View, Text, StyleSheet, ScrollView, FlatList } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { useGame } from '../context/GameContext';
import { useWallet } from '../context/WalletContext';
import MinecraftCard from '../components/MinecraftCard';

export default function HistoryScreen() {
  const { gameHistory } = useGame();
  const { transactions } = useWallet();

  const formatDate = (timestamp) => {
    const date = new Date(timestamp);
    return date.toLocaleString('ru-RU');
  };

  const getGameIcon = (gameType) => {
    const icons = {
      slots: '',
      roulette: '',
      coin_flip: '',
      dice: '',
    };
    return icons[gameType] || '';
  };

  const renderGameItem = ({ item }) => (
    <View style={styles.historyItem}>
      <Text style={styles.historyIcon}>{getGameIcon(item.gameType)}</Text>
      <View style={styles.historyContent}>
        <Text style={styles.historyTitle}>
          {item.gameType === 'slots' && ''}
          {item.gameType === 'roulette' && ''}
          {item.gameType === 'coin_flip' && '/'}
          {item.gameType === 'dice' && ''}
        </Text>
        <Text style={styles.historyDate}>{formatDate(item.timestamp)}</Text>
      </View>
      <View style={styles.historyAmount}>
        <Text style={[styles.historyAmountText, item.isWin ? styles.win : styles.loss]}>
          {item.isWin ? '+' : '-'}{item.betAmount.toFixed(2)} TON
        </Text>
        {item.isWin && (
          <Text style={styles.winAmount}>
            : {item.winAmount.toFixed(2)} TON
          </Text>
        )}
      </View>
    </View>
  );

  const renderTransactionItem = ({ item }) => (
    <View style={styles.historyItem}>
      <Text style={styles.historyIcon}>
        {item.type === 'deposit' ? '' : ''}
      </Text>
      <View style={styles.historyContent}>
        <Text style={styles.historyTitle}>
          {item.type === 'deposit' ? '' : ''}
        </Text>
        <Text style={styles.historyDate}>{formatDate(item.timestamp)}</Text>
      </View>
      <View style={styles.historyAmount}>
        <Text style={[styles.historyAmountText, item.type === 'deposit' ? styles.win : styles.loss]}>
          {item.type === 'deposit' ? '+' : '-'}{item.amount.toFixed(2)} TON
        </Text>
      </View>
    </View>
  );

  return (
    <LinearGradient
      colors={['#7CB342', '#5A8A2E']}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContent}>
        <MinecraftCard title="  ">
          {gameHistory.length === 0 ? (
            <Text style={styles.emptyText}>  </Text>
          ) : (
            <FlatList
              data={gameHistory.slice().reverse()}
              renderItem={renderGameItem}
              keyExtractor={(item) => item.id.toString()}
              scrollEnabled={false}
            />
          )}
        </MinecraftCard>

        <MinecraftCard title="  ">
          {transactions.length === 0 ? (
            <Text style={styles.emptyText}>  </Text>
          ) : (
            <FlatList
              data={transactions.slice().reverse()}
              renderItem={renderTransactionItem}
              keyExtractor={(item) => item.id.toString()}
              scrollEnabled={false}
            />
          )}
        </MinecraftCard>
      </ScrollView>
    </LinearGradient>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollContent: {
    padding: 16,
  },
  historyItem: {
    flexDirection: 'row',
    alignItems: 'center',
    padding: 12,
    marginBottom: 8,
    backgroundColor: 'rgba(0, 0, 0, 0.2)',
    borderRadius: 8,
  },
  historyIcon: {
    fontSize: 24,
    marginRight: 12,
  },
  historyContent: {
    flex: 1,
  },
  historyTitle: {
    fontSize: 16,
    color: '#FFFFFF',
    fontWeight: 'bold',
    marginBottom: 4,
  },
  historyDate: {
    fontSize: 12,
    color: '#FFFFFF',
    opacity: 0.7,
  },
  historyAmount: {
    alignItems: 'flex-end',
  },
  historyAmountText: {
    fontSize: 16,
    fontWeight: 'bold',
  },
  win: {
    color: '#4CAF50',
  },
  loss: {
    color: '#F44336',
  },
  winAmount: {
    fontSize: 12,
    color: '#FFD700',
    marginTop: 4,
  },
  emptyText: {
    fontSize: 14,
    color: '#FFFFFF',
    opacity: 0.7,
    textAlign: 'center',
    padding: 20,
  },
});
