import React, { useState } from 'react';
import { View, Text, StyleSheet, TextInput, Alert, ScrollView } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { useWallet } from '../../context/WalletContext';
import { useGame } from '../../context/GameContext';
import MinecraftCard from '../../components/MinecraftCard';
import MinecraftButton from '../../components/MinecraftButton';

export default function WithdrawScreen() {
  const { walletAddress, isConnected, addTransaction } = useWallet();
  const { balance, updateBalance } = useGame();
  const [withdrawAddress, setWithdrawAddress] = useState(walletAddress || '');
  const [amount, setAmount] = useState('');

  const handleWithdraw = async () => {
    if (!withdrawAddress.trim()) {
      Alert.alert('', '   ');
      return;
    }

    const withdrawAmount = parseFloat(amount);
    
    if (!withdrawAmount || withdrawAmount <= 0) {
      Alert.alert('', '  ');
      return;
    }

    if (withdrawAmount < 1) {
      Alert.alert('', '  : 1 TON');
      return;
    }

    if (withdrawAmount > balance) {
      Alert.alert('', '   ');
      return;
    }

    Alert.alert(
      ' ',
      ` ${withdrawAmount.toFixed(2)} TON  :\n${withdrawAddress.substring(0, 20)}...?`,
      [
        {
          text: '',
          style: 'cancel',
        },
        {
          text: '',
          onPress: async () => {
            try {
              //          TON API
              await updateBalance(-withdrawAmount);
              await addTransaction({
                type: 'withdrawal',
                amount: withdrawAmount,
                address: withdrawAddress,
                status: 'pending',
              });
              
              Alert.alert(
                ' ',
                '    .      .',
                [{ text: 'OK' }]
              );
              
              setAmount('');
            } catch (error) {
              Alert.alert('', error.message);
            }
          },
        },
      ]
    );
  };

  if (!isConnected) {
    return (
      <LinearGradient
        colors={['#7CB342', '#5A8A2E']}
        style={styles.container}
      >
        <ScrollView contentContainerStyle={styles.scrollContent}>
          <MinecraftCard title="   ">
            <Text style={styles.warningText}>
                   TON-
            </Text>
          </MinecraftCard>
        </ScrollView>
      </LinearGradient>
    );
  }

  return (
    <LinearGradient
      colors={['#7CB342', '#5A8A2E']}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContent}>
        <MinecraftCard title="  ">
          <View style={styles.balanceContainer}>
            <Text style={styles.balanceLabel}>  :</Text>
            <Text style={styles.balanceAmount}> {balance.toFixed(2)} TON</Text>
          </View>

          <View style={styles.inputContainer}>
            <Text style={styles.label}>  :</Text>
            <TextInput
              style={styles.input}
              value={withdrawAddress}
              onChangeText={setWithdrawAddress}
              placeholder="EQD...  UQ..."
              placeholderTextColor="#9E9E9E"
              autoCapitalize="none"
              autoCorrect={false}
            />
          </View>

          <View style={styles.inputContainer}>
            <Text style={styles.label}>  (TON):</Text>
            <TextInput
              style={styles.input}
              value={amount}
              onChangeText={setAmount}
              keyboardType="decimal-pad"
              placeholder="1.0"
              placeholderTextColor="#9E9E9E"
            />
            <Text style={styles.hint}>: 1 TON</Text>
          </View>

          <View style={styles.infoContainer}>
            <Text style={styles.infoTitle}>:</Text>
            <Text style={styles.infoItem}>  : 1 TON</Text>
            <Text style={styles.infoItem}>  : ~0.01 TON</Text>
            <Text style={styles.infoItem}>     24 </Text>
          </View>

          <MinecraftButton
            title="  "
            onPress={handleWithdraw}
            disabled={!amount || !withdrawAddress.trim() || parseFloat(amount) > balance}
            style={styles.withdrawButton}
          />
        </MinecraftCard>
      </ScrollView>
    </LinearGradient>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollContent: {
    padding: 16,
  },
  balanceContainer: {
    alignItems: 'center',
    marginBottom: 20,
    padding: 16,
    backgroundColor: 'rgba(0, 0, 0, 0.2)',
    borderRadius: 8,
  },
  balanceLabel: {
    fontSize: 14,
    color: '#FFFFFF',
    opacity: 0.8,
    marginBottom: 8,
  },
  balanceAmount: {
    fontSize: 24,
    fontWeight: 'bold',
    color: '#FFD700',
  },
  inputContainer: {
    marginBottom: 16,
  },
  label: {
    fontSize: 16,
    color: '#FFFFFF',
    marginBottom: 8,
  },
  input: {
    backgroundColor: 'rgba(0, 0, 0, 0.3)',
    borderWidth: 2,
    borderColor: '#5A8A2E',
    borderRadius: 8,
    padding: 12,
    color: '#FFFFFF',
    fontSize: 16,
  },
  hint: {
    fontSize: 12,
    color: '#FFD700',
    opacity: 0.8,
    marginTop: 4,
  },
  infoContainer: {
    marginBottom: 20,
    padding: 12,
    backgroundColor: 'rgba(0, 0, 0, 0.2)',
    borderRadius: 8,
  },
  infoTitle: {
    fontSize: 16,
    fontWeight: 'bold',
    color: '#FFD700',
    marginBottom: 8,
  },
  infoItem: {
    fontSize: 14,
    color: '#FFFFFF',
    opacity: 0.8,
    marginBottom: 4,
  },
  withdrawButton: {
    marginTop: 8,
  },
  warningText: {
    fontSize: 16,
    color: '#FFFFFF',
    textAlign: 'center',
    padding: 20,
  },
});
