import React, { useState } from 'react';
import { View, Text, StyleSheet, TextInput, Alert, ScrollView, TouchableOpacity } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { useGame } from '../../context/GameContext';
import MinecraftCard from '../../components/MinecraftCard';
import MinecraftButton from '../../components/MinecraftButton';
import { Minigame } from '../../utils/games';

export default function CoinFlipGameScreen() {
  const { balance, playGame } = useGame();
  const [choice, setChoice] = useState(null);
  const [betAmount, setBetAmount] = useState('');
  const [isFlipping, setIsFlipping] = useState(false);
  const [lastResult, setLastResult] = useState(null);

  const handleFlip = async () => {
    if (!choice) {
      Alert.alert('', '  ');
      return;
    }

    const bet = parseFloat(betAmount);
    
    if (!bet || bet <= 0) {
      Alert.alert('', '   ');
      return;
    }

    if (bet > balance) {
      Alert.alert('', ' ');
      return;
    }

    setIsFlipping(true);
    
    try {
      const result = Minigame.coinFlip(bet, choice);
      await playGame('coin_flip', bet, result);
      setLastResult(result);
    } catch (error) {
      Alert.alert('', error.message);
    } finally {
      setIsFlipping(false);
    }
  };

  return (
    <LinearGradient
      colors={['#7CB342', '#5A8A2E']}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContent}>
        <MinecraftCard title="   ">
          {lastResult && (
            <View style={styles.resultContainer}>
              <Text style={styles.coinEmoji}>
                {lastResult.result === 'heads' ? '' : ''}
              </Text>
              <Text style={styles.resultText}>
                : {lastResult.result === 'heads' ? '' : ''}
              </Text>
              {lastResult.isWin ? (
                <Text style={styles.winText}>
                     {lastResult.winAmount.toFixed(2)} TON!
                </Text>
              ) : (
                <Text style={styles.loseText}>  </Text>
              )}
            </View>
          )}

          <View style={styles.choiceContainer}>
            <Text style={styles.label}> :</Text>
            <View style={styles.choiceButtons}>
              <TouchableOpacity
                style={[styles.choiceButton, choice === 'heads' && styles.choiceButtonActive]}
                onPress={() => setChoice('heads')}
              >
                <Text style={styles.choiceButtonText}> </Text>
              </TouchableOpacity>
              <TouchableOpacity
                style={[styles.choiceButton, choice === 'tails' && styles.choiceButtonActive]}
                onPress={() => setChoice('tails')}
              >
                <Text style={styles.choiceButtonText}> </Text>
              </TouchableOpacity>
            </View>
          </View>

          <View style={styles.betContainer}>
            <Text style={styles.label}>  (TON):</Text>
            <TextInput
              style={styles.input}
              value={betAmount}
              onChangeText={setBetAmount}
              keyboardType="decimal-pad"
              placeholder="0.1"
              placeholderTextColor="#9E9E9E"
            />
            <Text style={styles.balanceText}>
              : {balance.toFixed(2)} TON
            </Text>
          </View>

          <MinecraftButton
            title={isFlipping ? "..." : " "}
            onPress={handleFlip}
            disabled={isFlipping || !betAmount || !choice}
            style={styles.flipButton}
          />
        </MinecraftCard>
      </ScrollView>
    </LinearGradient>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollContent: {
    padding: 16,
  },
  resultContainer: {
    alignItems: 'center',
    marginBottom: 20,
    padding: 16,
    backgroundColor: 'rgba(0, 0, 0, 0.2)',
    borderRadius: 8,
  },
  coinEmoji: {
    fontSize: 64,
    marginBottom: 16,
  },
  resultText: {
    fontSize: 20,
    color: '#FFFFFF',
    marginBottom: 8,
  },
  winText: {
    fontSize: 18,
    color: '#FFD700',
    fontWeight: 'bold',
  },
  loseText: {
    fontSize: 18,
    color: '#F44336',
  },
  choiceContainer: {
    marginBottom: 16,
  },
  label: {
    fontSize: 16,
    color: '#FFFFFF',
    marginBottom: 12,
  },
  choiceButtons: {
    flexDirection: 'row',
    justifyContent: 'space-around',
  },
  choiceButton: {
    backgroundColor: 'rgba(0, 0, 0, 0.3)',
    borderWidth: 2,
    borderColor: '#5A8A2E',
    borderRadius: 8,
    padding: 20,
    width: '45%',
    alignItems: 'center',
  },
  choiceButtonActive: {
    backgroundColor: '#7CB342',
    borderColor: '#FFD700',
  },
  choiceButtonText: {
    color: '#FFFFFF',
    fontSize: 18,
    fontWeight: 'bold',
  },
  betContainer: {
    marginBottom: 16,
  },
  input: {
    backgroundColor: 'rgba(0, 0, 0, 0.3)',
    borderWidth: 2,
    borderColor: '#5A8A2E',
    borderRadius: 8,
    padding: 12,
    color: '#FFFFFF',
    fontSize: 18,
    marginBottom: 8,
  },
  balanceText: {
    fontSize: 14,
    color: '#FFD700',
  },
  flipButton: {
    marginBottom: 16,
  },
});
