import React, { useState } from 'react';
import { View, Text, StyleSheet, ScrollView, TouchableOpacity, Alert } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { useWallet } from '../../context/WalletContext';
import { useGame } from '../../context/GameContext';
import MinecraftCard from '../../components/MinecraftCard';
import MinecraftButton from '../../components/MinecraftButton';
import * as Clipboard from 'expo-clipboard';

export default function DepositScreen() {
  const { walletAddress, isConnected, addTransaction, formatBalance } = useWallet();
  const { updateBalance } = useGame();
  const [depositAddress] = useState('EQD__________________________________________0vo'); //    

  const copyToClipboard = async () => {
    try {
      await Clipboard.setStringAsync(depositAddress);
      Alert.alert('', '    ');
    } catch (error) {
      Alert.alert('', '   ');
    }
  };

  const handleTransactionSent = async () => {
    //          API
    Alert.alert(
      ' ',
      '          .',
      [
        {
          text: 'OK',
          onPress: async () => {
            //  :   
            //         TON API
            const testAmount = 1.0; // 1 TON  
            await addTransaction({
              type: 'deposit',
              amount: testAmount,
              status: 'pending',
            });
            // await updateBalance(testAmount);
          }
        }
      ]
    );
  };

  if (!isConnected) {
    return (
      <LinearGradient
        colors={['#7CB342', '#5A8A2E']}
        style={styles.container}
      >
        <ScrollView contentContainerStyle={styles.scrollContent}>
          <MinecraftCard title="   ">
            <Text style={styles.warningText}>
                   TON-
            </Text>
          </MinecraftCard>
        </ScrollView>
      </LinearGradient>
    );
  }

  return (
    <LinearGradient
      colors={['#7CB342', '#5A8A2E']}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContent}>
        <MinecraftCard title="  ">
          <Text style={styles.description}>
             TON      
          </Text>

          <View style={styles.addressContainer}>
            <Text style={styles.label}>  :</Text>
            <View style={styles.addressBox}>
              <Text style={styles.addressText} numberOfLines={2}>
                {depositAddress}
              </Text>
              <TouchableOpacity
                style={styles.copyButton}
                onPress={copyToClipboard}
              >
                <Text style={styles.copyButtonText}> </Text>
              </TouchableOpacity>
            </View>
          </View>

          <View style={styles.infoContainer}>
            <Text style={styles.infoTitle}>:</Text>
            <Text style={styles.infoItem}>  : 1 TON</Text>
            <Text style={styles.infoItem}>     " "</Text>
            <Text style={styles.infoItem}>       </Text>
          </View>

          <MinecraftButton
            title="  "
            onPress={handleTransactionSent}
            style={styles.confirmButton}
          />
        </MinecraftCard>
      </ScrollView>
    </LinearGradient>
  );
}

