import React, { useState } from 'react';
import { View, Text, StyleSheet, TextInput, Alert, ScrollView, TouchableOpacity } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { useGame } from '../../context/GameContext';
import MinecraftCard from '../../components/MinecraftCard';
import MinecraftButton from '../../components/MinecraftButton';
import { Minigame } from '../../utils/games';

export default function DiceGameScreen() {
  const { balance, playGame } = useGame();
  const [prediction, setPrediction] = useState(null);
  const [betAmount, setBetAmount] = useState('');
  const [isRolling, setIsRolling] = useState(false);
  const [lastResult, setLastResult] = useState(null);

  const handleRoll = async () => {
    if (!prediction) {
      Alert.alert('', ' ');
      return;
    }

    const bet = parseFloat(betAmount);
    
    if (!bet || bet <= 0) {
      Alert.alert('', '   ');
      return;
    }

    if (bet > balance) {
      Alert.alert('', ' ');
      return;
    }

    setIsRolling(true);
    
    try {
      const result = Minigame.diceRoll(bet, prediction);
      await playGame('dice', bet, result);
      setLastResult(result);
    } catch (error) {
      Alert.alert('', error.message);
    } finally {
      setIsRolling(false);
    }
  };

  return (
    <LinearGradient
      colors={['#7CB342', '#5A8A2E']}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContent}>
        <MinecraftCard title=" ">
          {lastResult && (
            <View style={styles.resultContainer}>
              <Text style={styles.diceEmoji}>
                {['', '', '', '', '', '', ''][lastResult.result]}
              </Text>
              <Text style={styles.resultText}>
                : {lastResult.result}
              </Text>
              <Text style={styles.predictionText}>
                 : {lastResult.prediction}
              </Text>
              {lastResult.isWin ? (
                <Text style={styles.winText}>
                     {lastResult.winAmount.toFixed(2)} TON!
                </Text>
              ) : (
                <Text style={styles.loseText}>  </Text>
              )}
            </View>
          )}

          <View style={styles.predictionContainer}>
            <Text style={styles.label}>  (1-6):</Text>
            <View style={styles.diceGrid}>
              {[1, 2, 3, 4, 5, 6].map((num) => (
                <TouchableOpacity
                  key={num}
                  style={[
                    styles.diceButton,
                    prediction === num && styles.diceButtonActive,
                  ]}
                  onPress={() => setPrediction(num)}
                >
                  <Text style={styles.diceButtonText}>{num}</Text>
                </TouchableOpacity>
              ))}
            </View>
          </View>

          <View style={styles.betContainer}>
            <Text style={styles.label}>  (TON):</Text>
            <TextInput
              style={styles.input}
              value={betAmount}
              onChangeText={setBetAmount}
              keyboardType="decimal-pad"
              placeholder="0.1"
              placeholderTextColor="#9E9E9E"
            />
            <Text style={styles.balanceText}>
              : {balance.toFixed(2)} TON
            </Text>
          </View>

          <MinecraftButton
            title={isRolling ? "..." : " "}
            onPress={handleRoll}
            disabled={isRolling || !betAmount || !prediction}
            style={styles.rollButton}
          />
        </MinecraftCard>
      </ScrollView>
    </LinearGradient>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollContent: {
    padding: 16,
  },
  resultContainer: {
    alignItems: 'center',
    marginBottom: 20,
    padding: 16,
    backgroundColor: 'rgba(0, 0, 0, 0.2)',
    borderRadius: 8,
  },
  diceEmoji: {
    fontSize: 64,
    marginBottom: 16,
  },
  resultText: {
    fontSize: 20,
    color: '#FFFFFF',
    marginBottom: 8,
  },
  predictionText: {
    fontSize: 16,
    color: '#FFD700',
    marginBottom: 8,
  },
  winText: {
    fontSize: 18,
    color: '#FFD700',
    fontWeight: 'bold',
  },
  loseText: {
    fontSize: 18,
    color: '#F44336',
  },
  predictionContainer: {
    marginBottom: 16,
  },
  label: {
    fontSize: 16,
    color: '#FFFFFF',
    marginBottom: 12,
  },
  diceGrid: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
  },
  diceButton: {
    width: 60,
    height: 60,
    margin: 8,
    borderRadius: 8,
    backgroundColor: 'rgba(0, 0, 0, 0.3)',
    borderWidth: 2,
    borderColor: '#5A8A2E',
    alignItems: 'center',
    justifyContent: 'center',
  },
  diceButtonActive: {
    backgroundColor: '#7CB342',
    borderColor: '#FFD700',
  },
  diceButtonText: {
    color: '#FFFFFF',
    fontSize: 24,
    fontWeight: 'bold',
  },
  betContainer: {
    marginBottom: 16,
  },
  input: {
    backgroundColor: 'rgba(0, 0, 0, 0.3)',
    borderWidth: 2,
    borderColor: '#5A8A2E',
    borderRadius: 8,
    padding: 12,
    color: '#FFFFFF',
    fontSize: 18,
    marginBottom: 8,
  },
  balanceText: {
    fontSize: 14,
    color: '#FFD700',
  },
  rollButton: {
    marginBottom: 16,
  },
});
