import React from 'react';
import { View, Text, StyleSheet, ScrollView } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { useNavigation } from '@react-navigation/native';
import { useWallet } from '../context/WalletContext';
import { useGame } from '../context/GameContext';
import MinecraftCard from '../components/MinecraftCard';
import MinecraftButton from '../components/MinecraftButton';

export default function WalletScreen() {
  const navigation = useNavigation();
  const { isConnected, walletAddress } = useWallet();
  const { balance } = useGame();

  const formatBalance = (amount) => {
    if (amount >= 1) {
      return `${amount.toFixed(2)} TON`;
    }
    return `${(amount * 1000).toFixed(0)} TON`;
  };

  return (
    <LinearGradient
      colors={['#7CB342', '#5A8A2E']}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContent}>
        <MinecraftCard title=" ">
          <View style={styles.balanceContainer}>
            <Text style={styles.balanceAmount}> {formatBalance(balance)}</Text>
            <Text style={styles.balanceLabel}>  </Text>
          </View>
        </MinecraftCard>

        <MinecraftCard title=" TON-">
          {isConnected ? (
            <View style={styles.walletConnected}>
              <Text style={styles.walletStatus}> </Text>
              <Text style={styles.walletAddress} numberOfLines={1}>
                {walletAddress}
              </Text>
              <MinecraftButton
                title=" "
                onPress={() => navigation.navigate('ConnectWallet')}
                style={styles.walletButton}
              />
            </View>
          ) : (
            <View style={styles.walletDisconnected}>
              <Text style={styles.walletStatus}>  </Text>
              <Text style={styles.walletHint}>
                 TON-     
              </Text>
              <MinecraftButton
                title=" "
                onPress={() => navigation.navigate('ConnectWallet')}
                style={styles.walletButton}
              />
            </View>
          )}
        </MinecraftCard>

        <View style={styles.actionsContainer}>
          <MinecraftButton
            title=" "
            onPress={() => navigation.navigate('Deposit')}
            disabled={!isConnected}
            style={styles.actionButton}
          />
          <MinecraftButton
            title=" "
            onPress={() => navigation.navigate('Withdraw')}
            disabled={!isConnected || balance <= 0}
            style={styles.actionButton}
          />
        </View>
      </ScrollView>
    </LinearGradient>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollContent: {
    padding: 16,
  },
  balanceContainer: {
    alignItems: 'center',
    paddingVertical: 20,
  },
  balanceAmount: {
    fontSize: 32,
    fontWeight: 'bold',
    color: '#FFD700',
    marginBottom: 8,
    textShadowColor: '#000',
    textShadowOffset: { width: 1, height: 1 },
    textShadowRadius: 2,
  },
  balanceLabel: {
    fontSize: 14,
    color: '#FFFFFF',
    opacity: 0.8,
  },
  walletConnected: {
    alignItems: 'center',
  },
  walletDisconnected: {
    alignItems: 'center',
  },
  walletStatus: {
    fontSize: 18,
    color: '#FFFFFF',
    marginBottom: 12,
  },
  walletAddress: {
    fontSize: 12,
    color: '#FFD700',
    fontFamily: 'monospace',
    marginBottom: 16,
    textAlign: 'center',
  },
  walletHint: {
    fontSize: 14,
    color: '#FFFFFF',
    opacity: 0.8,
    textAlign: 'center',
    marginBottom: 16,
  },
  walletButton: {
    marginTop: 8,
  },
  actionsContainer: {
    marginTop: 16,
  },
  actionButton: {
    marginBottom: 12,
  },
});
