//     Telegram Web App

export const isTelegramWeb = () => {
  if (typeof window === 'undefined') return false;
  return window.Telegram && window.Telegram.WebApp;
};

export const getTelegramWebApp = () => {
  if (isTelegramWeb()) {
    return window.Telegram.WebApp;
  }
  return null;
};

export const getTelegramUser = () => {
  const webApp = getTelegramWebApp();
  if (webApp && webApp.initDataUnsafe && webApp.initDataUnsafe.user) {
    return webApp.initDataUnsafe.user;
  }
  return null;
};

export const getTelegramInitData = () => {
  const webApp = getTelegramWebApp();
  if (webApp) {
    return webApp.initData;
  }
  return null;
};

export const expandTelegramWebApp = () => {
  const webApp = getTelegramWebApp();
  if (webApp) {
    webApp.expand();
  }
};

export const closeTelegramWebApp = () => {
  const webApp = getTelegramWebApp();
  if (webApp) {
    webApp.close();
  }
};

export const setTelegramHeaderColor = (color) => {
  // setHeaderColor     6.0+ Telegram Web App
  //   ,   
  const webApp = getTelegramWebApp();
  if (webApp) {
    //  ,  
    const version = webApp.version || '6.0';
    const majorVersion = parseInt(version.split('.')[0], 10);
    
    //   6.0+   
    if (majorVersion >= 6) {
      return; //   
    }
    
    //      
    if (typeof webApp.setHeaderColor === 'function') {
      try {
        webApp.setHeaderColor(color);
      } catch (error) {
        console.warn('setHeaderColor error:', error);
      }
    }
  }
};

export const setTelegramBackgroundColor = (color) => {
  const webApp = getTelegramWebApp();
  // setBackgroundColor     6.0+
  if (webApp && typeof webApp.setBackgroundColor === 'function') {
    try {
      webApp.setBackgroundColor(color);
    } catch (error) {
      //  ,    
      console.warn('setBackgroundColor not supported:', error);
    }
  }
};

export const enableTelegramClosingConfirmation = () => {
  const webApp = getTelegramWebApp();
  if (webApp) {
    webApp.enableClosingConfirmation();
  }
};

export const disableTelegramClosingConfirmation = () => {
  const webApp = getTelegramWebApp();
  if (webApp) {
    webApp.disableClosingConfirmation();
  }
};

// Haptic feedback
export const hapticFeedback = (type = 'impact', style = 'medium') => {
  const webApp = getTelegramWebApp();
  if (webApp && webApp.HapticFeedback) {
    if (type === 'impact') {
      webApp.HapticFeedback.impactOccurred(style);
    } else if (type === 'notification') {
      webApp.HapticFeedback.notificationOccurred(style);
    } else if (type === 'selection') {
      webApp.HapticFeedback.selectionChanged();
    }
  }
};
